
import Jumbotron from './components/Jumbotron/Jumbotron.vue'
import HomeBanner from './components/HomeBanner.vue'
import HomeGames from './components/HomeGames.vue'
import HomeWorks from './components/HomeWorks.vue'
import HomeResume from './components/HomeResume.vue'
import { getHomeInfo } from '@/services/home.service.js'
import homeInfo from '~/public/simulate_data/home.json'

import { Options, Vue } from 'vue-class-component'

@Options({
  name: 'Home',
  components: {
    Jumbotron,
    HomeBanner,
    HomeGames,
    HomeWorks,
    HomeResume
  },
  data () {
    return {
      bannerList: [],
      homePortfolio: {}
    }
  },
  created () {
    this.getHomeInfo2()
  },
  methods: {
    async getHomeInfo2 (): Promise<void> {
      let data: any = {}
      try {
        const res = await getHomeInfo()
        data = res.data
      } catch (e) {
        data = homeInfo.data
      }
      this.bannerList = data.banner || []
      this.homePortfolio = data.homePortfolio || {}
    }
  }
})

export default class Home extends Vue {}

