
import './resource/js/rAF.js'
import demoInit from './resource/js/demo-2.js'
import { Options, Vue } from 'vue-class-component'
@Options({
  data () {
    return {
      backgroundImage: {}
    }
  },
  activated () {
    this.init()
  },
  mounted () {
    console.log('mounted')
    demoInit()
  },
  methods: {
    init () {
      const random = Number.parseInt((Math.random() * (7 - 1) + 1).toString())
      console.log('random', random)
      this.backgroundImage = {
        backgroundImage: `url(/static/Jumbotron/img/demo-${random}-bg.jpg)`
      }
    }
  }
})
export default class Jumbotron extends Vue {}
